.row{
    margin-left: 20px;
    color: white;
}
.row__posters{
    display: flex;
    overflow-x: hidden;
    overflow-y: scroll;
    padding: 20px;
}
.row__posters::-webkit-scrollbar{
    display: none;
}

.row__poster{
    object-fit: contain;
    width: 100%;
    max-height: 100px;
    transition: transform 450ms;
    margin-right: 10px;
}
.row__posterLarge{
    max-height: 200px;
}
.row__poster:hover{
    transform: scale(1.3);
}
